import React from 'react'
import Container from 'react-bootstrap/Container'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import logoc3 from './images/logoc3.png'

const Footer = () => (
    <Container fluid className="contact">
        <footer>
            <Row className="justify-content-center">
                <img src={logoc3} alt="logo"/>
                <Col md={5} className="text-center">
                <p>C3 Alive is a Ugandan region office part of the C3 Global movement. We are 
                    committed to healthy relationships and connection between ministers and churches. 
                    We are part of an international family, our members are able to walk into any of
                    our C3 Churches around the world and know they’re home. We are united with Christ in
                    His vision, and therefore our mission, to save the lost, make disciples and build
                    His church.</p>
                    <strong>Contact Info</strong>
                    <p>(+256)-800-250-914<br/>c3alive@gmail.com</p>
                </Col>
            </Row>
            <hr className="socket bg-dark "/>
            <div class="footer-bottom">
                &copy; C3 Alive Church | All Rights Reserved
            </div>
     

        </footer> 
    </Container>
)

export default Footer;